.list-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 0 0 10px;
}

.list-item label {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  color: #005665;
}

.list-item p {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 33px;
  color: #000000;
}

@media (max-width: 414px) {
  .list-item {
    margin: 0;
  }
}